.App {
  background: #222222;
  height: 100%;
  width: 100%;
  position: relative;
  font-family: 'Tajawal';
  color: #dddddd;
}

.appContainer {
  background-image: -webkit-gradient(linear,left top, left bottom,from(rgba(34,34,34,0.5)),to(rgba(34,34,34,0.5))), url('./assets/nikita-kachanovsky-445394-unsplash.jpg');
  background-image: -webkit-linear-gradient(rgba(34,34,34,0.5),rgba(34,34,34,0.5)), url('./assets/nikita-kachanovsky-445394-unsplash.jpg');
  background-image: -o-linear-gradient(rgba(34,34,34,0.5),rgba(34,34,34,0.5)), url('./assets/nikita-kachanovsky-445394-unsplash.jpg');
  background-image: linear-gradient(rgba(34,34,34,0.5),rgba(34,34,34,0.5)), url('./assets/nikita-kachanovsky-445394-unsplash.jpg');
  background-size: 100%;
  background-repeat: no-repeat;
  height: 100%;
  width: 100%;
  position: relative;
}

.App::after {
  height: 100%;
  width: 100%;
  opacity: 0.5;
  background: #222222;
  position: absolute;
}

.page {
  min-height: 100%;
  width: 100%;
  background: #222222;
}

.canvas {
  position: absolute;
  z-index: -1;
}


